___CSS_LOADER_EXPORT___.push([module.id, `/* style for Wind Container */

/*========== Parent Cont ==========*/
main > div:nth-child(6) {
    font-size: clamp(16px, 1.5cqw, 2rem);

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    align-items: center;
    justify-items: center;
}

/*========== Child divs ==========*/
main > div:nth-child(6) div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/*========== inner P ==========*/
main > div:nth-child(6) div > p {
    font-weight: bold;
}

/*========== inner values ==========*/
main > div:nth-child(6) div > p:last-child {
    color: var(--gray1);
    margin-top: 1.5rem;
}

/*========== arrow ==========*/
main > div:nth-child(6) div img {
    width: 28px;
    height: 28px;
    margin-top: 1.5rem;
    transition: transform 0.5s ease;
}

/*========== measurement units ==========*/
main > div:nth-child(6) div > p:last-child::after{
    color: var(--gray2);
    font-size: 1rem;
    font-weight: 100;
}

main > div:nth-child(6) div:is(:nth-child(1), :nth-child(2)) > p:last-child::after {
    content: "mph";
}


/*========== Media Queries ==========*/
/* Tablet: 1024px */

@media (max-width: 1200px) {
    
    /*========== Parent Cont ==========*/
    main > div:nth-child(6) {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 1fr;

        padding: 5px
    }

    /*========== inner P values==========*/
    main > div:nth-child(6) div > p:last-child {
        margin-top: 0.5rem;
    }

    /*========== arrow ==========*/
    main > div:nth-child(6) div img {
        height: 24px;
        margin-top: 0.5rem;
    }
}

/* Phone: 360 */
@media (max-width: 550px), screen and (orientation: portrait) {
     /*========== inner values ==========*/
    main > div:nth-child(6) div > p:last-child {
        margin-top: 0.2rem;
    }

    /*========== arrow ==========*/
    main > div:nth-child(6) div img {
        height: 20px;
        margin-top: 0.2rem;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/wind.css"],"names":[],"mappings":"AAAA,6BAA6B;;AAE7B,oCAAoC;AACpC;IACI,oCAAoC;;IAEpC,aAAa;IACb,qCAAqC;IACrC,kCAAkC;IAClC,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA,mCAAmC;AACnC;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA,gCAAgC;AAChC;IACI,iBAAiB;AACrB;;AAEA,qCAAqC;AACrC;IACI,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA,8BAA8B;AAC9B;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,+BAA+B;AACnC;;AAEA,0CAA0C;AAC1C;IACI,mBAAmB;IACnB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,cAAc;AAClB;;;AAGA,sCAAsC;AACtC,mBAAmB;;AAEnB;;IAEI,oCAAoC;IACpC;QACI,aAAa;QACb,qCAAqC;QACrC,uBAAuB;;QAEvB;IACJ;;IAEA,sCAAsC;IACtC;QACI,kBAAkB;IACtB;;IAEA,8BAA8B;IAC9B;QACI,YAAY;QACZ,kBAAkB;IACtB;AACJ;;AAEA,eAAe;AACf;KACK,qCAAqC;IACtC;QACI,kBAAkB;IACtB;;IAEA,8BAA8B;IAC9B;QACI,YAAY;QACZ,kBAAkB;IACtB;AACJ","sourcesContent":["/* style for Wind Container */\r\n\r\n/*========== Parent Cont ==========*/\r\nmain > div:nth-child(6) {\r\n    font-size: clamp(16px, 1.5cqw, 2rem);\r\n\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    grid-template-rows: repeat(2, 1fr);\r\n    align-items: center;\r\n    justify-items: center;\r\n}\r\n\r\n/*========== Child divs ==========*/\r\nmain > div:nth-child(6) div {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n/*========== inner P ==========*/\r\nmain > div:nth-child(6) div > p {\r\n    font-weight: bold;\r\n}\r\n\r\n/*========== inner values ==========*/\r\nmain > div:nth-child(6) div > p:last-child {\r\n    color: var(--gray1);\r\n    margin-top: 1.5rem;\r\n}\r\n\r\n/*========== arrow ==========*/\r\nmain > div:nth-child(6) div img {\r\n    width: 28px;\r\n    height: 28px;\r\n    margin-top: 1.5rem;\r\n    transition: transform 0.5s ease;\r\n}\r\n\r\n/*========== measurement units ==========*/\r\nmain > div:nth-child(6) div > p:last-child::after{\r\n    color: var(--gray2);\r\n    font-size: 1rem;\r\n    font-weight: 100;\r\n}\r\n\r\nmain > div:nth-child(6) div:is(:nth-child(1), :nt