___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --gray1: #303030;
    --gray2: #404040;
    --font1: 'Inter', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /*   outline: 1px solid red;     */
}

body {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    
    animation: gradient-anim 3s infinite;
    background-size: 200%, 200%;
    background-color:var(--bg-1);
    background-image:var(--bg-img-1);

    transition-property: background-color, background-image;
    transition-duration: 2s;
    transition-delay: 3s;
    transition-timing-function: ease-in;

    overflow: hidden;
}

.filter {
    background-color: rgba(16, 0, 60, 0.5);
    position: absolute;
    width: 100%;
    height: 100%;
}

main {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(8, 1fr);
    gap: 10px;
    padding: 10px;
}

.preloader {
    position: absolute;
    width: 100%;
    height: 100%;
    inset: 0;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    backdrop-filter: blur(20px);

    display: none;
    align-items: center;
    justify-content: center;
}

.preloader img {
    width: 100px;
    height: auto;
}

.visible {
    display: flex;
}

/*========== INNER CONTAINERS ==========*/
.cont {
    border-radius: 5px;
    font-family: var(--font1);
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.1));
    backdrop-filter: blur(10px);
    border: 3px solid rgba(255, 255, 255, 0.4);
    border-right: 3px solid rgba(255, 255, 255, 0.1);
    border-bottom: 3px solid rgba(255, 255, 255, 0.1);
    box-shadow: 5px 5px 2px rgba(0, 0, 0, 0.2);
}

/* Current Weather */
main > div:nth-child(3) {
   grid-column: 1 / 3;
   grid-row: 2 / 9;
}

/* Location Cont */
main > div:nth-child(4) {
    grid-column: 3 / 5;
    grid-row: 1 / 5;
}

/* Wind Cont */
main > div:nth-child(6) {
    grid-row: 2 / 5;
}

/* Forecast Cont */
main > div:nth-child(7) {
    grid-column: 3 / 6;
    grid-row: 5 / 9;
}


/*========== Media Queries ==========*/
/* Tablet: 1024px */
@media (max-width: 1200px) {
    main {
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: repeat(9, 1fr);
    }

    /* Logo Cont */
    main > div:nth-child(1) {
        grid-column: 1 / 3;
        grid-row: 1 / 2;
    }
    
    /* Position Cont */
    main > div:nth-child(2) {
        grid-column: 3 / 5;
        grid-row: 1 / 2;
    }

    /* Search Cont */
    main > div:nth-child(5) {
        grid-column: 5 / 7;
        grid-row: 1 / 2;
    }

    /* Current Weather */
    main > div:nth-child(3) {
        grid-column: 1 / 4;
        grid-row: 2 / 6;
     }
     
     /* Location Cont */
     main > div:nth-child(4) {
        grid-column: 4 / 7;
        grid-row: 2 / 5;
     }
     
     /* Wind Cont */
     main > div:nth-child(6) {
        grid-column: 4 / 7;
        grid-row: 5 / 6;
     }
     
     /* Forecast Cont */
     main > div:nth-child(7) {
        grid-column: 1 / 7  ;
        grid-row: 6 / 10;
     }
}

/* Phone: 360 */
@media (max-width: 550px), screen and (orientation: portrait) {
    main {
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: 0.2fr repeat(2, 0.3fr) repeat(3, 0.3fr) 0.2fr repeat(2, 0.6fr);
        gap: 5px;
    }

    /* Logo Cont */
    main > div:nth-child(1) {
        grid-column: 1 / 3;
        grid-row: 1 / 2;
     }

    /* Position Cont */
    main > div:nth-child(2) {
        grid-column: 3 / 4;
        grid-row: 1 / 2;
     }
    
    /* Search Cont */
    main > div:nth-child(5) {
        grid-column: 4 / 6;
        grid-row: 1 / 2;
    }

    /* Current Weather */
    main > div:nth-child(3) {
        grid-column: 1 / 6;
        grid-row: 4 / 7;
     }
     
     /* Location Cont */
     main > div:nth-child(4) {
         grid-column: 1 / 6;
         grid-row: 2 / 4;
     }
     
     /* Wind Cont */
     main > div:nth-child(6) {
        grid-column: 1 / 6;
         grid-row: 7 / 8;
     }
     
     /* Forecast Cont */
     main > div:nth-child(7) {
         grid-column: 1 / 6;
         grid-row: 8 / 10;
     }
}

/*========== Animation/s ==========*/
@keyframes gradient-anim {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 100%;
	}
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAEA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,4BAA4B;AAChC;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,kCAAkC;AACtC;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,eAAe;;IAEf,oCAAoC;IACpC,2BAA2B;IAC3B,4BAA4B;IAC5B,gCAAgC;;IAEhC,uDAAuD;IACvD,uBAAuB;IACvB,oBAAoB;IACpB,mCAAmC;;IAEnC,gBAAgB;AACpB;;AAEA;IACI,sCAAsC;IACtC,kBAAkB;IAClB,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,YAAY;;IAEZ,aAAa;IACb,qCAAqC;IACrC,kCAAkC;IAClC,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,QAAQ;IACR,mDAAmU;IACnU,2BAA2B;;IAE3B,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA,yCAAyC;AACzC;IACI,kBAAkB;IAClB,yBAAyB;IACzB,uFAAuF;IACvF,2BAA2B;IAC3B,0CAA0C;IAC1C,gDAAgD;IAChD,iDAAiD;IACjD,0CAA0C;AAC9C;;AAEA,oBAAoB;AACpB;GACG,kBAAkB;GAClB,eAAe;AAClB;;AAEA,kBAAkB;AAClB;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA,cAAc;AACd;IACI,eAAe;AACnB;;AAEA,kBAAkB;AAClB;IACI,kBAAkB;IAClB,eAAe;AACnB;;;AAGA,sCAAsC;AACtC,mBAAmB;AACnB;IACI;QACI,qCAAqC;QACrC,kCAAkC;IACtC;;IAEA,cAAc;IACd;QACI,kBAAkB;QAClB,eAAe;IACnB;;IAEA,kBAAkB;IAClB;QACI,kBAAkB;QAClB,eAAe;IACnB;;IAEA,gBAAgB;IAChB;QACI,kBAAkB;QAClB,eAAe;IACnB;;IAEA,oBAAoB;IACpB;QACI,kBAAkB;QAClB,eAAe;KAClB;;KAEA,kBAAkB;KAClB;QACG,kBAAkB;QAClB,eAAe;KAClB;;KAEA,cAAc;KACd;QACG,kBAAkB;QAClB,eAAe;KAClB;;KAEA,kBAAkB;KAClB;QACG,oBAAoB;QACpB,gBAAgB;KACnB;AACL;;AAEA,eAAe;AACf;IACI;QACI,qCAAqC;QACrC,kFAAkF;QAClF,QAAQ;IACZ;;IAEA,cAAc;IACd;QACI,kBAAkB;QAClB,eAAe;KAClB;;IAED,kBAAkB;IAClB;QACI,kBAAkB;QAClB,eAAe;KAClB;;IAED,gBAAgB;IAChB;QACI,kBAAkB;QAClB,eAAe;IACnB;;IAEA,oBAAoB;IACpB;QACI,kBAAkB;QAClB,eAAe;KAClB;;KAEA,kBAAkB;KAClB;SACI,kBAAkB;SAClB,eAAe;KACnB;;KAEA,cAAc;KACd;QACG,kBAAkB;SACjB,eAAe;KACnB;;KAEA,kBAAkB;KAClB;SACI,kBAAkB;SAClB,gBAAgB;KACpB;AACL;;AAEA,oCAAoC;AACpC;CACC;EACC,2BAA2B;CAC5B;CACA;EACC,6BAA6B;CAC9B;CACA;EACC,4BAA4B;CAC7B;AACD","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');\r\n\r\n:root {\r\n    --gra