        grid-template-rows: 1fr 0.5fr repeat(7, 1fr);
        padding: 5px;
    }

    /*========== temperature ==========*/
    main > div:nth-child(3) > p:nth-child(3) {
        grid-column: 1 / 4;
        grid-row: 3 / 7;
    }

    /*========== image ==========*/
    main > div:nth-child(3) > img:nth-child(4) {
        width: 70px;
        padding-top: 1rem;
        grid-column: 4 / 6;
        grid-row: 3 / 5;
        justify-self: center;
    }

    /*========== feels like ==========*/
    main > div:nth-child(3) > p:nth-child(5) {
        grid-column: 4 / 5;
        grid-row: 5 / 7;
        font-size: clamp(14px, 2cqw, 3rem);
        padding: 0;
        justify-self: center;
    }

    main > div:nth-child(3) > p:nth-child(5) > span{
        font-size: clamp(20px, 3cqw, 4rem);
        display: block;
    }   

    /*========== uv index ==========*/
    main > div:nth-child(3) > p:nth-child(6) {
        grid-column: 5 / 6;
        grid-row: 5 / 7;
        font-size: clamp(14px, 2cqw, 3rem);
        padding: 0;
        justify-self: center;
    }

    main > div:nth-child(3) > p:nth-child(6) > span{
        font-size: clamp(20px, 3cqw, 4rem);
        display: block;
    }   

    /*========== divider ==========*/
    main > div:nth-child(3) > div:nth-child(7) {
        grid-column: 2 / 5;
        grid-row: 7 / 8;
        
        border-left: none;
        border-top: 2px solid var(--gray2);
        height: 0;
        width: 100%;   
    }

    /*========== right container ==========*/
    main > div:nth-child(3) > div:nth-child(8) {
        width: 100%;
        grid-column: 1 / 6;
        grid-row: 8 / 10;
        justify-self: center;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
        margin: none;
        text-align: center;
    }

}

