        grid-row: 4 / 5;
        text-align: left;
    }

    /*========== divider ==========*/
    main > div:nth-child(3) > div:nth-child(7) {
        display: none;
    }

    /*========== right container ==========*/
    main > div:nth-child(3) > div:nth-child(8) {
        grid-column: 1 / 6;
        grid-row: 5 / 6;
    }
