___CSS_LOADER_EXPORT___.push([module.id, `/* style for search, get location, and logo */

/*========== Search Cont ==========*/
main > div:nth-child(5) form{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5%;
}

main > div:nth-child(5)  input {
   background-color: transparent;
   /* font-size: min(1rem, 3cqw); */
   font-size: clamp(0.6rem, 3cqw, 1rem);
   height: 2rem;
   width: 60%;
   padding-left: 5px;
   
   border: none;
   border-bottom: 1.5px solid gray;
   outline: none;
}

main > div:nth-child(5)  button {
    border: none;
    background-color: transparent;
}

main > div:nth-child(5)  img {
    width: 25px;
}


/*========== Location Cont ==========*/
main > div:nth-child(2) {
   display: flex;
   align-items: center;
   justify-content: center;
}

main > div:nth-child(2) > button{
    font-size: 1rem;
    padding: 5px 2cqi;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 5px;
    border: 2px solid rgba(255, 255, 255, 0.5);

    display: flex;
    align-items: center;
}

main > div:nth-child(2) > button > img {
    width: 25px;
    height: auto;
}

main > div:nth-child(2) > button::after {
    content: "\\00a0 \\00a0 My Position"
}

/*========== Logo Cont ==========*/
main > div:nth-child(1) {
    font-size: clamp(0.6rem, 3cqw, 1.5rem);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    font-weight: bold;
}
 
main > div:nth-child(1) img {
    width: min(2.5rem, 6cqi);
    height: auto;
}
 

/*========== Media Queries ==========*/
/* Tablet: 1024px */

/* Phone: 360 */
@media (max-width: 550px), screen and (orientation: portrait) {

    /* Search input */
    main > div:nth-child(5)  input {
        height: 1.5rem;
    }

    /* Search magnifier */
    main > div:nth-child(5)  img {
        width: 25px;
    }

    /* Position */
    main > div:nth-child(2) > button{
        background-color: transparent;
        border: none;
    }

    main > div:nth-child(2) > button::after {
        content: ""
    }

    /* logo img */
    main > div:nth-child(1) img {
        width: 25px;
    }
}

@media (max-width: 300px){
        /* logo title */
        main > div:nth-child(1) > p {
            display: none;
        }
}`, "",{"version":3,"sources":["webpack://./src/styles/others.css"],"names":[],"mappings":"AAAA,6CAA6C;;AAE7C,oCAAoC;AACpC;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,OAAO;AACX;;AAEA;GACG,6BAA6B;GAC7B,gCAAgC;GAChC,oCAAoC;GACpC,YAAY;GACZ,UAAU;GACV,iBAAiB;;GAEjB,YAAY;GACZ,+BAA+B;GAC/B,aAAa;AAChB;;AAEA;IACI,YAAY;IACZ,6BAA6B;AACjC;;AAEA;IACI,WAAW;AACf;;;AAGA,sCAAsC;AACtC;GACG,aAAa;GACb,mBAAmB;GACnB,uBAAuB;AAC1B;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,0CAA0C;IAC1C,kBAAkB;IAClB,0CAA0C;;IAE1C,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI;AACJ;;AAEA,kCAAkC;AAClC;IACI,sCAAsC;IACtC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,wBAAwB;IACxB,YAAY;AAChB;;;AAGA,sCAAsC;AACtC,mBAAmB;;AAEnB,eAAe;AACf;;IAEI,iBAAiB;IACjB;QACI,cAAc;IAClB;;IAEA,qBAAqB;IACrB;QACI,WAAW;IACf;;IAEA,aAAa;IACb;QACI,6BAA6B;QAC7B,YAAY;IAChB;;IAEA;QACI;IACJ;;IAEA,aAAa;IACb;QACI,WAAW;IACf;AACJ;;AAEA;QACQ,eAAe;QACf;YACI,aAAa;QACjB;AACR","sourcesContent":["/* style for search, get location, and logo */\r\n\r\n/*========== Search Cont ==========*/\r\nmain > div:nth-child(5) form{\r\n    height: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    gap: 5%;\r\n}\r\n\r\nmain > div:nth-child(5)  input {\r\n   background-color: transparent;\r\n   /* font-size: min(1rem, 3cqw); */\r\n   font-size: clamp(0.6rem, 3cqw, 1rem);\r\n   height: 2rem;\r\n   width: 60%;\r\n   padding-left: 5px;\r\n   \r\n   border: none;\r\n   border-bottom: 1.5px solid gray;\r\n   outline: none;\r\n}\r\n\r\nmain > div:nth-child(5)  button {\r\n    border: none;\r\n    background-color: transparent;\r\n}\r\n\r\nmain > div:nth-child(5)  img {\r\n    width: 25px;\r\n}\r\n\r\n\r\n/*========== Location Cont ==========*/\r\nmain > div:nth-child(2) {\r\n   display: flex;\r\n   align-items: center;\r\n   justify-content: center;\r\n}\r\n\r\nmain > div:nth-child(2) > button{\r\n    font-size: 1rem;\r\n    padding: 5px 2cqi;\r\n    background-color: rgba(255, 255, 255, 0